.podokonniki {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    flex: 1;
    gap: 50px;
}

.podokonniki .category.big:nth-child(1) {
    grid-column-start: 3;
    grid-column-end: 7;
}

.podokonniki .category.big:nth-child(2) {
    grid-column-start: 7;
    grid-column-end: 11;
}