.header {
    background-color: #fff;
    box-shadow: 0px -4px 10px 0px black;
    padding: 10px;
    height: 80px;
    display: flex;
    justify-content: center;
}

.header .content {
    height: 100%;
    justify-content: center;
    align-items: center;
}

.header .content * {
    max-height: 50px;
    height: auto;
}

.header .logo {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
}

.header .navigation {
    grid-column-start: 3;
    grid-column-end: 8;
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;
}

.header .nav {
    color: black;
    text-decoration: none;
    padding: 4px;
}

.header .nav.selected {
    font-weight: bold;
    border-bottom: 1px solid #3D699E;
    color: #3D699E;
}

.header .nav:not(.selected):hover {
    cursor: pointer;
    color: #3D699E;
}

.header .contacts {
    grid-column-start: 8;
    grid-column-end: 11;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header .head-btn {
    grid-column-start: 11;
    grid-column-end: 13;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 6px;
    background: #3d699f;
    background: linear-gradient(135deg, rgba(61, 105, 159, 0.7455357142857143) 0%, rgba(243, 82, 93, 0.75) 100%);
    color: white;
    padding: 10px;
    height: auto;
}

.nav-group {
    position: relative;
}

.nav-list {
    position: absolute;
    /* border: 1px solid black; */
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    left: -16px;
    z-index: 1;
    height: max-content;
    margin-top: 10px;
    max-height: 200px !important;
    /* border-radius: 12px; */
    /* overflow: hidden; */
    /* box-shadow: 0px 0px 10px -4px black; */
    /* background-color: black; */
    gap: 10px;
    /* overflow: hidden; */
}

.nav-item {
    position: relative;
    padding: 10px;
    background-color: #fff;
    width: calc(100% - 20px);
    text-align: center;
    border-radius: 12px;
    box-shadow: 0px 0px 10px -6px #3D699E;
}

.nav-item:hover {
    cursor: pointer;
    filter: brightness(0.9);
}