.colors {
    display: flex;
    justify-content: space-between;
}

.marble-img {
    width: 15%;
}

.marble .full {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.marble-win-img {
    user-select: none;
    pointer-events: none;
    grid-column-start: 1;
    grid-column-end: 7;
    width: 100%;
    border-radius: 18px;
}

.marble-about {
    grid-column-start: 7;
    grid-column-end: 13;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.marble-about.left {
    grid-column-start: 1;
    grid-column-end: 7;
}

.marble-about.frist {
    grid-row-start: 1;
}

.marble-about-block {
    display: flex;
    flex-direction: column;
    gap: 10px;
}