.slider {
    display: grid;
    grid-template-columns: repeat(12, 60px);
    gap: 20px;
    align-items: center;
    justify-content: center;
}

.slide-control {
    width: 40px;
    height: 40px;
    background-color: #ffffff;
    border-radius: 40px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
    justify-self: center;
    transition: background-color .2s;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
}

.slide-control:hover {
    cursor: pointer;
    background-color: #ededed;
}

.slider__content {
    height: 497px;
    grid-column-start: 2;
    grid-column-end: 12;
    background-color: #515E8E;
    border-radius: 18px;
    overflow: hidden;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}

.slider__navigation {
    grid-column-start: 1;
    grid-column-end: 13;
    display: flex;
    justify-content: center;
    gap: 20px;
    user-select: none;
}

.slider__nav {
    width: 16px;
    height: 16px;
    border-radius: 20px;
    background-color: #D9D9D9;
    transition: background-color 1s, filter .2s;
}

.slider__nav.selected {
    background-color: #979797;
}

.slider__nav:not(.selected):hover {
    cursor: pointer;
    filter: brightness(.8)
}

.slides {
    width: auto;
    transition: margin-left 1s;
    height: 100%;
    display: flex;
}

.slide {
    flex: 1;
    width: 100%;
    height: 100%;
    pointer-events: none;
    /* float: left; */
}

.slide-control-arrow {
    width: 50%;
}