.windows-pvh {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    gap: 50px;
}

.windows-pvh .content {
    row-gap: 40px;
}

.window-pvh-card {
    grid-column-start: 1;
    grid-column-end: 13;
    padding: 20px;
    box-shadow: 0px 0px 16px #00000040;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    gap: 10px;
}

.window-pvh-card-title {
    font-size: 30px;
    font-weight: bold;
}

.window-pvh-card-que {
    font-size: 20px;
}