.home {
    display: flex;
    flex-direction: column;
    gap: 100px;
}

.welcome .content {
    align-items: center;
}

.welcome__txt {
    grid-column-start: 1;
    grid-column-end: 6;
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: black;
}

.welcome__title {
    font-size: 24px;
    font-weight: bold;
}

.welcome__win {
    grid-column-start: 7;
    grid-column-end: 13;
    display: grid;
    grid-template-columns: repeat(6, 60px);
    grid-template-rows: repeat(2, 220px);
    gap: 20px;
}

.welcome__win__card {
    background-color: #515E8E;
    padding: 20px;
    /* width: calc(100% - 40px); */
    /* margin: auto; */
    /* background: silver; */
    /* overflow: hidden; */
    border-radius: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: calc(220px - 40px);
}

.welcome__win__card:nth-child(odd) {
    grid-column-start: 1;
    grid-column-end: 4;
}

.welcome__win__card:nth-child(even) {
    grid-column-start: 4;
    grid-column-end: 7;
}

.color-on-blue {
    /* position: absolute; */
    background: linear-gradient(103.41deg, #EB565B 0%, #68F251 100%);
    border-radius: 12px;
    width: 100%;
    height: 131px;
}

.color-on-white {
    background: linear-gradient(225deg, #3D699E 0%, #68F251 100%);
    border-radius: 12px;
    height: 260px;
    width: 260px;
}

.welcome__win__card__title {
    color: white;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.block.red .content {
    grid-template-columns: repeat(3, 300px);
}

.action-card {
    background-color: #fff;
    padding: 20px;
    border-radius: 18px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    gap: 20px;
}

.action-card__title {
    font-size: 16px;
    font-weight: bold;
}

.action-card__desc {
    font-size: 14px;
}

.action-card__buy-zone {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: end;
}

.action-card__prices {
    position: relative;
}

.action-card__prices .old-price {
    position: absolute;
    left: 50%;
    bottom: 80%;
    width: max-content;
    color: #A3A3A3;
    font-size: 14px;
    text-decoration: line-through;
}

.action-card__prices .cur-price {
    font-size: 18px;
    font-weight: bold;
}

.action-card__buy-zone .buy-btn {
    font-size: 14px;
    padding: 10px 20px;
    color: white;
    background-color: #67DF5D;
    border-radius: 6px;
    text-decoration: none;
}

.action-card__buy-zone .buy-btn:hover {
    filter: brightness(1.1);
}

.category {
    background-color: #515E8E;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-radius: 18px;
    user-select: none;
}

.category:hover {
    cursor: pointer;
    filter: brightness(1.2);
}

.category-name {
    color: white;
    font-size: 18px;
}

.category-img {
    border-radius: 12px;
    pointer-events: none;
}

.category:nth-child(1) {
    grid-column-start: 1;
    grid-column-end: 5;
}

.category:nth-child(2) {
    grid-column-start: 5;
    grid-column-end: 9;
}

.category:nth-child(3) {
    grid-column-start: 9;
    grid-column-end: 13;
}

.category:nth-child(4) {
    grid-column-start: 1;
    grid-column-end: 4;
}

.category:nth-child(5) {
    grid-column-start: 4;
    grid-column-end: 7;
}

.category:nth-child(6) {
    grid-column-start: 7;
    grid-column-end: 10;
}

.category:nth-child(7) {
    grid-column-start: 10;
    grid-column-end: 13;
}

.block.partners .content {
    row-gap: 50px;
}

.partner-imgs {
    grid-column-start: 1;
    grid-column-end: 13;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.partner {
    width: 150px;
}