.materials {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: start;
    align-items: center;
    flex-shrink: 0;
    flex: 1;
    gap: 50px;
}

.acryl-img {
    grid-column-start: 1;
    grid-column-end: 7;
    border-radius: 12px;
    width: 100%;
    pointer-events: none;
    user-select: none;
}

.acryl-about {
    grid-column-start: 7;
    grid-column-end: 13;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.materials h2 {
    margin: 0;
    text-align: left;
}

.acryl-text {
    margin: 0;
}

.acryl-about.mini {
    grid-column-start: 1;
    grid-column-end: 5;
}

.acryl-about.big {
    grid-column-start: 5;
    grid-column-end: 13;
}

span.big {
    font-size: 18px;
    text-align: center;
    width: 100%;
}