.windows-alum {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    gap: 50px;
}

.windows-alum .desk-block {
    grid-column-start: 1;
    grid-column-end: 13;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    gap: 10px;
}

.windows-alum .content {
    row-gap: 50px;
}

.content.alum-cards {
    row-gap: 20px;
}

.desk-txt p,
.desk-txt ul {
    margin: 0;
    margin-left: 24px;
}

.desk-txt {
    font-size: 18px;
}

.alum-card {
    padding: 20px;
    border-radius: 12px;
    background: #FFFFFF;
    box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    transition: transform .25s ease, box-shadow .25s ease;
}

.alum-card:hover {
    transform: scale(1.01);
    box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.25);
    cursor: pointer;
}

.alum-card.min {
    /* aspect-ratio: 1/1; */
}

.alum-card-name {
    /* height: 80px; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    flex-shrink: 0;
}

.alum-card.min.left {
    grid-column-start: 1;
    grid-column-end: 7;
}


/* .alum-card.min.center {
    grid-column-start: 5;
    grid-column-end: 9;
} */

.alum-card.min.right {
    grid-column-start: 7;
    grid-column-end: 13;
}

.alum-card.u-min.right {
    grid-column-start: 9;
    grid-column-end: 13;
}

.alum-card.u-min.left {
    grid-column-start: 1;
    grid-column-end: 5;
}


/* .alum-card.max {
    grid-column-start: 1;
    grid-column-end: 13;
} */

.alum-card.max.left {
    grid-column-start: 1;
    grid-column-end: 9;
}

.alum-card.max.right {
    grid-column-start: 5;
    grid-column-end: 13;
}

.alum-card-name {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
}

.alum-card-img {
    width: 100%;
    object-fit: cover;
    border-radius: 6px;
    pointer-events: none;
}

.alum-card.min .alum-card-img {
    height: 315px;
}

.alum-card.u-min .alum-card-img {
    height: 286px;
}

.alum-card.max .alum-card-img {
    object-fit: fill;
}