.wins-n-doors {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    flex: 1;
    flex-shrink: 0;
    gap: 50px;
}

.win-nav {
    grid-column-start: 1;
    grid-column-end: 13;
    display: flex;
    justify-content: start;
    gap: 10px;
    margin-top: -25px;
}

.win-nav-item {
    padding: 10px;
    border-radius: 500px;
    border: 1px solid black;
    user-select: none;
    background-color: #fff;
}

.win-nav-item:not(.selected):hover {
    background-color: #dfdfdf;
    cursor: pointer;
}

.win-nav-item.selected {
    box-shadow: 0px 0px 0px 4px #515E8E;
    /* border-color: #515E8E; */
    border: none;
}