.App {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 50px;
    /* padding: 60px; */
}

.content {
    display: grid;
    grid-template-columns: repeat(12, 60px);
    gap: 20px;
}

.block {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.block.red {
    background-color: #F2515E;
    padding: 50px;
    width: calc(100% - 100px);
}

.block.blue {
    background-color: #515E8E;
    padding: 50px;
    width: calc(100% - 100px);
}

.block.categories .content {
    /* display: flex; */
}

.title {
    grid-column-start: 1;
    grid-column-end: 13;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
}

h2 {
    font-size: 26px;
    font-weight: bold;
    text-align: center;
}

a {
    color: black;
    text-decoration: none;
}

.full {
    grid-column-start: 1;
    grid-column-end: 13;
}

.flex {
    display: flex;
}

.column {
    flex-direction: column;
}

.jcsb {
    justify-content: space-between;
}

.row {
    flex-direction: row;
}

.link {
    text-decoration: underline;
    user-select: none;
}

.link:hover {
    cursor: pointer;
    color: #515E8E;
    /* filter: brightness(.8); */
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 6px;
    background: #3d699f;
    background: linear-gradient(135deg, rgba(61, 105, 159, 0.7455357142857143) 0%, rgba(243, 82, 93, 0.75) 100%);
    color: white;
    padding: 10px;
    height: auto;
    user-select: none;
    transition: filter .2s;
}

.btn:hover {
    cursor: pointer;
    filter: brightness(1.2);
}