.footer {
    height: 180px !important;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #515E8E;
}

.footer .content {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    width: 940px;
}

.footer__title {
    font-size: 30px;
    font-weight: bold;
    color: white;
    text-align: center;
}

.footer__contacts {
    display: flex;
    justify-content: space-between;
}

.footer__contact {
    font-size: 20px;
    /* font-weight: bold; */
    color: white;
}