.podokonniki.stone {
    justify-content: start;
}

.podokonniki.stone .full {
    gap: 20px;
}

.pod-stone-block {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: start;
}

.pod-stone-block.row {
    gap: 20px;
    align-items: stretch;
}

.pod-stone-title {
    font-size: 26px;
    font-weight: bold;
}

.pod-stone-img {
    width: 50%;
    border-radius: 12px;
}

.pod-stone-text-block {
    width: 50%;
    gap: 10px;
}