.win-open-systems {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    /* height: 100%; */
    flex: 1;
    gap: 50px;
}

.win-open-systems .full {
    gap: 10px;
}

.win-open-systems h2 {
    text-align: left;
}

.win-open-systems .full.left {
    grid-column-start: 1;
    grid-column-end: 7;
}

.win-open-systems .full.right {
    grid-column-start: 7;
    grid-column-end: 13;
}

.win-open-systems .full.mini {
    grid-column-start: 1;
    grid-column-end: 6;
}

.win-open-systems .full.big {
    grid-column-start: 6;
    grid-column-end: 13;
}

span.mini {
    font-size: 14px;
}